.agenda-section {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #e0dfd6;
  overflow: hidden;
  /* border: 1px solid red; */
}
.agenda-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem;
  /* overflow-y: auto; */
  overflow: hidden;
  border: 1px solid blue;
}
.agenda-header {
  flex-shrink: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-height: 30vh;
  border: 1px solid red;
}
.agenda-header h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem;
  font-size: 2rem;
}
.agenda-header button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin: 0 0.5rem;
  border-color: transparent;
  background-color: transparent;
  font-size: 1.3rem;
  color: #a9a397;
  cursor: pointer;
}
.agenda-header button:hover {
  color: rgba(0, 0, 0, 1);
}
.agenda-calendar {
  /* position: relative; */
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 0.5rem 0;
  overflow: hidden;
  border: 1px solid red;
}
.agenda-status {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
  background-color: #f2eee7;
  color: #4f4b45;
}
.calendar-container {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  overflow-y: auto;

  /* border: 1px solid red; */
}
.calendar-weekdays {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #e0d6c5;
  color: #a9a397;
  /* border: 1px solid red; */
}
.calendar-week {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 0.75rem; */
  font-weight: bold;
  /* font-size: 1.2rem; */
  color: #4f4b45;
  /* border: 1px solid red; */

  padding: 0.35rem;
  font-size: 1.1rem;
}
@media screen and (max-width: 479px) {
  .agenda-container {
    margin: 0.25rem 0.5rem;
    padding: 0.25rem;
    /**/
    position: relative;
  }
  .agenda-header {
    min-height: 10vh;
    margin: 0;
    margin-bottom: 0.25rem;
    padding: 0.25rem 0;
  }
  .agenda-header h3 {
    padding: 0.25rem;
    margin: 0;
    font-size: 1.5rem;
  }
  .agenda-header button {
    margin: 0;
    padding: 0.25rem;
    font-size: 1rem;
  }
  .agenda-calendar {
    margin: 0.25rem 0;
  }
  .agenda-status {
    font-size: 0.9rem;
  }

  .calendar-week {
    /* padding: 0.5rem; */
    padding: 0.25rem;
    font-size: 1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .agenda-container {
    margin: 0.25rem 0.5rem;
    padding: 0.25rem;
    /**/
    position: relative;
  }
  .agenda-header {
    min-height: 10vh;
    margin: 0;
    margin-bottom: 0.25rem;
    padding: 0.25rem 0;
  }
  .agenda-header h3 {
    padding: 0.25rem;
    margin: 0;
    font-size: 1.6rem;
  }
  .agenda-header button {
    margin: 0;
    padding: 0.25rem;
    font-size: 1.05rem;
  }
  .agenda-calendar {
    margin: 0.25rem 0;
  }
  .agenda-status {
    font-size: 0.95rem;
  }
  .calendar-week {
    /* padding: 0.5rem; */
    padding: 0.25rem;
    font-size: 1.05rem;
  }
}

@media screen and (min-width: 768px) {
  .agenda-container {
    padding: 0.5rem 1rem;
  }
  .agenda-header h3 {
    font-size: 2rem;
  }
  .agenda-header button {
    font-size: 1.3rem;
  }
  .agenda-calendar {
    position: relative;
    margin: 0.5rem 0;
  }
  .agenda-status {
    padding: 0.25rem 0.5rem;
    font-size: 1rem;
  }
  .calendar-week {
    padding: 0.35rem;
    font-size: 1.1rem;
  }
}
