.agenda-form-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0);
}
.agenda-form-center {
  position: absolute;
  width: 100%;
  bottom: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  background-color: #e0dfd6;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.25rem;
}
.agenda-form-title {
  flex-shrink: 0;
  min-height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: 1px solid red;
}
.agenda-form-title h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}
@media screen and (max-width: 479px) {
  .agenda-form-center {
    /* padding: 0.5rem; */
    padding: 0;
    padding: 0.25rem;
    /* margin: 0.25rem 0.5rem; */
    /* border: 1px solid red; */
  }
  .agenda-form-title {
    min-height: 10vh;
    margin: 0;
    margin-bottom: 0.25rem;
    padding: 0.15rem 0;
    padding: 0;
  }
  .agenda-form-title h3 {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .agenda-form-center {
    /* border: 1px solid purple; */
    padding: 0;
  }
}

