.list-container {
  flex: 1;
  flex-shrink: 0;
  min-height: 40vh;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 0.25rem;
  overflow-y: auto;
  background-color: #f2f1eb;
  margin-top: 0.25rem;
  margin-bottom: 0.5rem;
  /* border: 1px solid red; */
}
.list-center {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  /* border: 1px solid red; */
}
.list-item {
  display: flex;
  /* margin: 0.25rem; */
  /* padding: 0.25rem; */
  margin: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: #e8e3da;
  border-radius: 0.25rem;
  align-items: center;
}
.form-todo-input {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* border: 1px solid blue; */
}
/*이부분 다시 확인*/
.input-checkobx {
  margin: 0 1rem 0 0.5rem;
  color: #c8d5b9;
}
/*확인해보고 지우기*/
.input-checkobx:checked {
  color: gray;
  color: red;
}
.list-content {
  flex: 1;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  margin-left: 0.25rem;
  border-radius: 0.25rem;
  font-size: 1.1rem;
  word-break: break-word;
  /* border: 1px solid red; */
}
.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.25rem;
}
.edit-btn,
.remove-btn,
.complete-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  margin: 0 0.25rem;
  color: #5b5f52;
  background-color: transparent;
  border-color: transparent;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
}
.edit-btn:hover,
.remove-btn:hover,
.complete-btn:hover {
  color: #3e3b36;
}
.remove-btn:disabled {
  color: gray;
  cursor: default;
}
.completed {
  text-decoration: line-through;
  color: #000;
  /* color: #d4edda; */
}
@media screen and (max-width: 479px) {
  .list-item {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: 0.25rem;
    /* padding: 0.25rem; */
    padding: 0.25rem 0.5rem;
    /* border: 1px solid red; */
  }
  .list-content {
    padding: 0.5rem;
    font-size: 1rem;
  }
  .btn-container {
    justify-content: flex-start;
    margin: 0;
    padding-left: 1rem;
    /* border: 1px solid red; */
  }
  .edit-btn,
  .remove-btn,
  .complete-btn {
    padding: 0.25rem;
    margin: 0;
    margin-right: 0.5rem;
    font-size: 0.9rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .list-item {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: 0.25rem;
    /* padding: 0.25rem; */
    padding: 0.25rem 0.5rem;
  }
  .list-content {
    padding: 0.5rem;
    font-size: 1.05rem;
  }
  .btn-container {
    justify-content: flex-start;
    margin: 0;
    padding-left: 1rem;
  }
